.popup {
  position: fixed;
  background-color: rgba(0, 0, 0, .5);
  width: 100%;
  min-width: 300px;
  height: 100vh;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center; 
}

.popup__container {
  padding: 30px;
  background-color: #ffffff;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  max-width: 530px;
  width: calc(100% - 80px);
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.popup__title {
  font-family: Inter, Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.5;
  text-align: center;
  color: #000000;
  margin: 0;
}

.popup__close {
  background-image: url(../../images/close-icon.svg);
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 0;
  width: 35px;
  height: 35px;
  position: absolute;
  top: 0px;
  right: 0px;
  border: none;
}

.popup__close:focus {
  outline: none;
}

.popup .error {
  color: var(--text-error);
}

.popup button {
  cursor: pointer;
}